}
.navComp{
    z-index: 5;
    position: absolute;
}
img {
    width: calc(100vw / 13);
}
.navComp ul{
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    width: calc(100vw / 8);
    height: 100vh;
    position: fixed;
    align-content: center;
    margin: 0px;
    box-shadow: 5px 0px 20px #ababab;
    background-color: white;
    padding: 0;
}
.navComp ul li{
    margin: 0 0 40px 0;
}
.navComp ul li a{
    text-decoration: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-content: center;
}
.navComp ul li a span{
    font-size: calc(100vw / 70);
    color: black;
    font-weight: 400;
}

@media (max-width: 1024px) and (orientation: portrait){
    img {
        width: calc(100vw / 6);
        max-width: 100px;
    }
    .navComp ul{
        justify-content: space-evenly;
        width: 100vw;
        height: 15vh;
    }
    .navComp ul li{
        margin: 0 0 0px 0;
        display: flex;
        flex-wrap: wrap;
    }
    .navComp ul li a span{
