*{
    box-sizing: border-box;
}
.bookComp{
    position: absolute;
    width: calc(100vw - (100vw / 8));
    height: 100vh;
    left: calc(100vw / 8);
    top: 50%;
    transform: translate(0%, -50%);
    /* background-color: rgb(234, 234, 234); */
}
.roomSelection{
    /* border: solid 1px black; */
    width: calc(100vw / 4);
    height: 50vh;
    position: absolute;
    left: 5%;
    top: 20%;
    box-shadow: 5px 0px 20px #ababab;
    border-top-left-radius: calc(100vw / 10);
}
.meetingDetails form div {
    flex: 100% 1;
}
.roomSelection h3{
    text-align: right;
}
.meetingDetails h3{
    text-align: center;
}
.meetingDetails h3, .roomSelection h3{
    width: 100%;
    padding: 2vw;
    font-size: 2.5vw;
    font-weight: 300;
    position: relative;
} 
.roomSelection form{
    height: calc((50vh) - 30%);
    position: absolute;
    top: 30%;
    display: flex;
    flex-wrap: wrap;
}
.roomSelection form label{
    flex: 50% 1;
    width: calc(100vw / 8);
}
.roomSelection form .flex{
    flex: 100% 1;
}
.roomSelection form .flex label{
    float: left;
}
.roomSelection form .flex input{
    float: right;
    margin-right: 40px;
}
input[type="radio"] {
    /* remove standard background appearance */
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    /* create custom radiobutton appearance */
    display: inline-block;
    width: 20px;
    height: 20px;
    padding: 2px;
    /* background-color only for content */
    background-clip: content-box;
    border: 1px solid black;
    background-color: #e7e6e7;
    border-radius: 50%;
  }
  
  /* appearance for checked radiobutton */
  input[type="radio"]:checked {
    outline: none;
  }
  #pinkRadio:checked{
    background-color: hotpink;
  }
  #orangeRadio:checked{
      background-color: orange;
  }
  #blueRadio:checked{
      background-color: rgb(78, 114, 255); 
  }
  input[type="radio"]:active {
      outline: none;
  }
.meetingDetails form label{
    flex: calc(50% - 40px) 1;
}
.meetingDetails form input{
    width: 200px;
    height: 30px;
    outline: none;
    border: none;
    border-radius: 2px;
    box-shadow: 0px 0px 2px 1px #ababab;
    margin-right: 40px;
}
.roomSelection form label, .meetingDetails form label{
    padding-left: 40px;
}
.roomSelection button, .meetingDetails button, .edit-overlay form button, .message-overlay button{
    width: calc(100vw / 10);
    font-size: 1.5vw;
    font-weight: 400;
    height: 50px;
    position: relative;
    left: 50%;
    transform: translate(-50%);
    border-radius: 10px;
    border: none;
    box-shadow: 0px 0px 15px #ababab;
}
.bookComp h1{
    font-weight: 400;
    position: absolute;
    top: 6%;
    left: 50%;
    transform: translate(-50%, 0%);
}
.meetingDetails{
    width: calc(100vw / 2);
    height: 70vh;
    position: absolute;
    left: 38%;
    top: 20%;
    box-shadow: 5px 0px 20px #ababab;
    border-top-right-radius: calc(100vw / 10);
    border-bottom-left-radius: calc(100vw / 10);
}
.meetingDetails form{
    height: calc((100vh) - 70%);
    position: absolute;
    /* top: 30%; */
    display: flex;
    flex-wrap: wrap;
}
.helpInfo{
    position: absolute;
    width: calc(100vw / 4);
    left: 5%;
    top: calc(20% + 55vh);
}
.message-overlay{
    /* border: 1px solid black; */
    background-color: white;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 3;
    box-shadow: rgba(0,0,0,0.6) 0px 0px 0 100vw;
    padding: 20px;
    width: calc(100% / 2.5);
    border-radius: 20px;
}
.message-overlay h1{
    position: relative;
    text-align: center;
}
.message-overlay button{
    position: relative;
    left:50%;
    margin-top: 20px;
    transform: translate(-50%, 0);
}
/* 5px 0px 20px #ababab; */
/* animations */
@keyframes blink {
    0%   { box-shadow: 5px 0px 20px 10px rgb(255, 214, 238); }
    33.3%  { box-shadow: 5px 0px 20px 10px rgb(255, 226, 182);}
    66.6% { box-shadow: 5px 0px 20px 10px rgb(191, 217, 255); }
    100% { box-shadow: 5px 0px 20px 10px rgb(255, 214, 238); }
}
.meetingDetails, .roomSelection {
    /* border: 5px solid; */
    animation: blink 10s infinite;
}
@keyframes backgroundColorPalette {
    0%   { background-color: rgb(255, 214, 238); color: hotpink; }
    33.3%  { background-color: rgb(255, 226, 182); color: orange; }
    66.6% { background-color: rgb(191, 217, 255); color: rgb(78, 114, 255); }
    100% { background-color: rgb(255, 214, 238); color: hotpink; }
}
.roomSelection button, .meetingDetails button{
    animation-name: backgroundColorPalette;
    animation-duration: 10s;
	animation-iteration-count: infinite;

}

.bookComp .roomOverlay, .bookComp .edit-overlay{
    position: absolute;
    width: calc(100% / 2.5);
    padding: 20px;
    box-shadow: rgba(0,0,0,0.6) 0px 0px 0 1000vw;
    border-radius: 10px; 
    z-index: 2;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.edit-overlay{
    background-color:white;
}
.bookComp .roomOverlay h3, .bookComp .edit-overlay h3{
    text-align: center;
    margin-bottom: 20px;
}
.bookComp .edit-overlay label{
    box-sizing: border-box;
    margin-bottom: 20px;
}
.bookComp .edit-overlay input{
    float: right;
    width: 45px;
}
#inputCapacity{
    border: none;
    box-shadow: 0px 0px 5px #ababab;
    text-align: right;
    border-radius: 5px;
}
#inputCapacity:focus {
    outline: none;
}
.bookComp .close{
    z-index: 5;
    top: -1vw;
    right: -1vw;
    position: absolute;
    display: block;
    float: right;
    border-radius: 50%;
    height: 2.5vw;
    width: 2.5vw;
    padding: 0;
    line-height: 0px;
    text-align: center;
}
.bookComp .close:active{
    outline: none;
}
.bookComp .close span{
    font-size: 3.5vw;
    font-weight: 200;
    position: absolute;
    top: 35%;
    left: 50%;
    transform: translate(-50%, 0%);
    margin: 0;
    padding: 0;
}

button:focus{
    outline: none;
}


