/* adjusting the built-in theme */
.tableComp {
  position: absolute;
  width: calc(100vw - (100vw / 8));
  height: 100vh;
  left: calc(100vw / 8);
  top: 50%;
  transform: translate(0%, -50%);
  z-index: 1;
  overflow-y: hidden;
}
.calendar_default_main, .calendar_default_scroll, {
  height: 30vh !important;
}
