.calendar_default_event_inner {
  display: flex;
  background: #ababab;
  color: #fff;
  border: 2px solid #888;
  font-size: 10pt;
  padding: 5px;
  opacity: 0.8;
}
.calendar_default_corner div:nth-of-type(2) {
  display: none !important;
}
