  .timetable-text h1 {
    font-size: 5.5vw;
  }
}
*{
    margin: 0;
}
.homeComp{
    position: absolute;
    width: calc(100vw - (100vw / 8));
    height: 100vh;
    left: calc(100vw / 8);
    top: 50%;
    transform: translate(0%, -50%);
    z-index: 1;
}
.homeComp h1{
    font-weight: 400;
    position: absolute;
    top: 3%;
    left: 50%;
    width: 80vw;
    text-align: center;
    transform: translate(-50%, 0%);
}
.homeComp h2{
    font-weight: 200;
    position: absolute;
    top: 10%;
    left: 50%;
    width: 80vw;
    text-align: center;
    transform: translate(-50%, 0%);
}
.roomTicket{
    width: calc(100vw / 4.5);
    height: 70vh;
    position: absolute;
    top: calc(100vh / 5);
    box-shadow: 5px 0px 20px #ababab;
}
.roomTicket h3{
    box-sizing: border-box;
    width: 100%;
    padding: calc(100vw / 50);
    font-size: calc(100vw / 40);
    font-weight: 300;
}
.roomSchedule{
    box-sizing: border-box;
    padding-top: 30px;
    padding-left: calc(100vw / 100);
    padding-right: calc(100vw / 100);
}
.meeting{
    margin-bottom: 15px;
}
.meetingTime{
    box-sizing: border-box;
    padding-right: 15px;
    padding-bottom: 10px;
}
.pink{
    background-color: rgb(255, 214, 238);
    left: calc(100% / 15);
    border-top-left-radius: calc(100vw / 10);
}
.pink h3{
    text-align: right;
    color: hotpink;
}
.orange{
    background-color: rgb(255, 226, 182);
    left: calc(100% / 2.8);
}
.orange h3{
    text-align: center;
    color: orange;
}
.blue{
    background-color: rgb(191, 217, 255);
    left: calc(100% / 1.55);
    border-top-right-radius: calc(100vw / 10);
}
.blue h3{
    color: rgb(78, 114, 255);
}

.homeComp .roomOverlay{
    box-sizing: border-box;
    position: absolute;
    top: calc(100vh / 5 + 80px);
    padding: 20px;
    background-color: white;
    width: calc(100vw / 4.5);
    height: calc(70vh - 80px);
    border-radius: 50px;
}
.roomOverlay p span{
    float: right;
    margin-right: 0;
}
/* .homeComp span, .homeComp{} */
@media (max-width: 1024px) and (orientation: portrait){
    *{
        box-sizing: border-box;
    }
    .homeComp{
        left: 0;
        top: 15vh;
        transform: translate(0%, 0%);
        position: fixed;
        overflow: auto;
        width: 100vw;
        height: 85vh;
    }
    .homeComp h1{
        top: 0;
        left: 0;
        transform: translate(0px, 0px);
        text-align: center;
        width: 100vw;
        margin-top: 7%;
        margin-bottom: 3%;
        position: relative;
        font-size: calc(100vw / 20);
    }
    .homeComp h2{
        font-weight: 200;
        font-size: calc(100vw / 30);
        position: relative;
        top: 0;
        left: 0;
        transform: translate(0, 0);
        width: 100vw;
        margin-bottom: 7%;
        padding-left: 10px;
        padding-right: 10px;
        text-align: center;
    }
    .roomTicket{
        height: auto;
        width: 95vw;
        position: relative;
        top: 0;
        padding-top: 10px;
        padding-bottom: 10px;
        margin-bottom: 5%;
        margin-left: 2.5vw;
        margin-right: 2.5vw;
        border-radius: 7px;
        left: 0;
    }
    .roomTicket h3{
        font-size: calc(100vw / 20);
        text-align: center;
    }
    .meeting{
        margin-left: 5%;
        margin-right: 5%;
    }
    .meeting span{
        font-size: calc(100vw / 40);
    }
    .homeComp .roomOverlay{
        width: 60vw;
        height: 30vh;
        position: fixed;
        top: 30%;
        left: 20% !important;
        border-radius: 10px; 
        box-shadow: rgba(0,0,0,0.6) 0px 0px 100px 100px;
        z-index: 7;
        display: flex;
        flex-wrap: wrap;
        align-content: space-between;
    }
    .close{
        z-index: 5;
        top: -2vw;
        right: -2vw;
        position: absolute;
        display: block;
        float: right;
        border-radius: 50%;
        height: 5vw;
        width: 5vw;
        padding: 0;
        line-height: 0px;
        text-align: center;
    }
    .close span{
        font-size: 7.5vw;
        font-weight: 100;
        position: absolute;
        top: 35%;
        left: 50%;
        transform: translate(-50%, 0%);
        margin: 0;
        padding: 0;
    }
    .roomOverlay h3{
        text-align: center;
        margin-bottom: 5%;
        margin-top: 2.5%;
        font-size: calc(100vw / 30);
        font-weight: 300;
    }
    .roomOverlay p{
        font-size: calc(100vw / 35);
        margin-bottom: 10px;
        margin-left: 3vw;
    }
    .roomOverlay p span{
        margin-right: 3vw;
    }
}
@media (max-width: 767px) and (orientation: portrait){
    .roomTicket{
        margin-left: 0;
        margin-right: 0;
        width: 100vw;
        border-radius: 0;
    }
    .homeComp h1, .roomTicket h3{
        font-size: calc(100vw / 13);
    }
    .homeComp h2{
        font-size: calc(100vw / 20);
    }
    .meeting{
        margin-left: 2%;
        margin-right: 2%;
    }
    .meeting span{
        font-size: calc(100vw / 20);
    }
    .homeComp .roomOverlay{
        width: 80vw;
        height: 50vh;
        top: 30%;
        left: 10% !important;
        box-shadow: rgba(0,0,0,0.6) 0px 0px 0 100vw;
    }
    .close {
        top: -3vw;
        right: -3vw;
        height: 8vw;
        width: 8vw;
        padding: 0;
        font-size: 5vw;
    }
    .close span{
        font-size: 12vw;
        font-weight: 100;
    }
    .roomOverlay h3{
        font-size: calc(100vw / 15);
        margin-bottom: 5vw;
    }
    .roomOverlay p{
        font-size: calc(100vw / 22);
        margin-left: 3vw;
        margin-bottom: 3vw;
    }
}
@media (max-width: 823px) and (orientation: landscape){
    .homeComp h1{
        font-size: 4vw;
        position: relative;
        margin-bottom: 10px;
        margin-top: 10px;
        top: 0;
    }
    .homeComp h2{
        font-size: 2.5vw;
        position: relative;
        top: 0;
    }
    .roomTicket{
        top: 25vh;
        height: auto;
        min-height: 70vh;
    }
    .roomSchedule{
        padding-top: 10px;
    }
    .homeComp .meeting span{
        font-size: calc(100vw / 75);
    }
}
